import { Injectable, Inject } from '@angular/core';
import { DOCUMENT } from '@angular/common';
import { WINDOW } from '../../win_tokens';

@Injectable({ providedIn: 'root' })
export class ScrollService {
  constructor(
    @Inject(WINDOW) private win: any,
    @Inject(DOCUMENT) private doc: any,
  ) {}

  /**
   * Ã¨Â®Â¾Ã§Â½Â®Ã¦Â»ÂÃ¥ÂÂ¨Ã¦ÂÂ¡Ã¨ÂÂ³Ã¦ÂÂÃ¥Â®ÂÃ¥ÂÂÃ§Â´Â 
   * @param element Ã¦ÂÂÃ¥Â®ÂÃ¥ÂÂÃ§Â´Â Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤ `document.body`
   * @param topOffset Ã¥ÂÂÃ§Â§Â»Ã¥ÂÂ¼Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤ `0`
   */
  scrollToElement(element?: Element, topOffset = 0) {
    if (!element) element = this.doc.body;

    element.scrollIntoView();

    const w = this.win;
    if (w && w.scrollBy) {
      w.scrollBy(0, element.getBoundingClientRect().top - topOffset);

      if (w.pageYOffset < 20) {
        w.scrollBy(0, -w.pageYOffset);
      }
    }
  }

  /**
   * Ã¦Â»ÂÃ¥ÂÂ¨Ã¨ÂÂ³Ã©Â¡Â¶Ã©ÂÂ¨
   * @param topOffset Ã¥ÂÂÃ§Â§Â»Ã¥ÂÂ¼Ã¯Â¼ÂÃ©Â»ÂÃ¨Â®Â¤ `0`
   */
  scrollToTop(topOffset = 0) {
    this.scrollToElement(this.doc.body, topOffset);
  }
}
